/* ------======GLOBAL STYLES======----- */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */
* {
  box-sizing: border-box;
}

main {
  overflow-x: hidden;
  min-height: 90vh;
}

.wrapper {
  width: 1440px;
  margin: 0 auto;
}

.ibg {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}

.ibg img {
  width: 0;
  height: 0;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
}

@mixin Roboto($size) {
  font-family: 'Roboto' !important;
  font-weight: $size !important;
}

@mixin Gotham($size1) {
    font-family: 'Gotham' !important;
    font-weight: $size1 !important;
}
.mobile{
    display: none;
}


