/* -------========SEARCH========------- */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */

.search{
    width: 100%;
    
    .wrapper{
        border: 2px solid #61A036;
        border-radius: 7px;
        display: flex;
        justify-content: space-between;
        margin-top: 37px;
        margin-bottom: 40px;

        input{
            width: 642px;
            padding: 14px 23px;
            background: 0;
            border: 0;
            width: 100%;
            @include Roboto(400);
            font-size: 18px;
            line-height: 175%;
    
            &::placeholder{
                @include Roboto(400);
                font-size: 18px;
                line-height: 175%;
                color: #828282;
            }
        }
    
        a{
            background: #61A036;
            min-width: 155px;
            padding: 13px 45px;
            font-size: 18px;
            line-height: 175%;
            color: #FFFFFF;
            @include Gotham(400);
        }
    }

    
}


