/* ------=======CATEGORIES=======------ */
/* ------------------------------------ */
/* ------------------------------------ */
/* ------------------------------------ */

.categories{
    width: 100%;

    .wrapper{
        display: flex;
        justify-content: center;

        .categories_blk{
            width: 263px;
            padding: 8px 31px 21px 31px;
            display: block;
            border: 1px solid #D2D2D2;
            box-sizing: border-box;
            border-radius: 7px;
            margin-right: 31px;

            &:last-child{
                margin-right: 0;
            }

            .blk_img{
                width: 200px;
                height: 162px;
                margin-bottom: 15px;
                
            }

            .blk_title{
                text-align: center;
                font-size: 24px;
                line-height: 28px;
                @include Roboto(500);
                color: #333333;
            }
        }
    }
}

